import React from "react";
import { Button } from "@mui/material";
import { Themecolors } from "api/Colors";

interface FilterButtonComponentProps {
  categories: string[];
  onButtonClick: (selectedCategory: string) => void;
}

const FilterButtonComponentProps: React.FC<FilterButtonComponentProps> = ({
  categories,
  onButtonClick,
}) => {
  const handleCategoryClick = (category: string) => {
    onButtonClick(category);
  };

  return (
    <div
      style={{
        display: "flex",
        width: "100%",
        justifyContent: "flex-end",
      }}
    >
      <div
        style={{
          display: "inline-flex",
          gap: "5px",
          marginRight: "5px",
        }}
      >
        {categories.map((category) => (
          <Button
            key={category}
            variant="outlined"
            sx={{
              flexGrow: 1,
              minWidth: "auto",
              height: "2.2em",
              color: Themecolors.Button2,
              backgroundColor: Themecolors.Button_bg3,
              "&:hover": {
                backgroundImage: Themecolors.B_hv3,
                borderColor: Themecolors.Button2,
                backgroundColor: Themecolors.Button_bg3,
              },
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
              fontSize: "0.9em",
              padding: "8px",
            }}
            onClick={() => {
              handleCategoryClick(category);
            }}
          >
            {category}
          </Button>
        ))}
      </div>
    </div>
  );
};

export default FilterButtonComponentProps;
